import * as React from "react";
import { MessageInput } from "@/components/assistant-ui/message-input";
import { useSearchParams } from "next/navigation";
import { useState, useEffect } from "react";
import { MessagesSquare } from "lucide-react";

interface ChatMessage {
  id: number;
  role: "user" | "assistant";
  content: string;
  timestamp: number;
}

export const Thread = () => {
  const searchParams = useSearchParams();
  const chatId = searchParams?.get('chat');
  const [messages, setMessages] = useState<ChatMessage[]>([]);

  // Load messages for current chat
  useEffect(() => {
    if (chatId) {
      const savedMessages = localStorage.getItem(`chat-${chatId}`);
      if (savedMessages) {
        setMessages(JSON.parse(savedMessages));
      } else {
        setMessages([]);
      }
    }
  }, [chatId]);

  // Save messages to localStorage
  useEffect(() => {
    if (chatId && messages.length > 0) {
      localStorage.setItem(`chat-${chatId}`, JSON.stringify(messages));
      
      // Update chat title in thread list
      const chats = JSON.parse(localStorage.getItem('rgs-chats') || '[]');
      const existingChat = chats.find((c: any) => c.id === chatId);
      
      if (existingChat) {
        existingChat.messages = messages;
        existingChat.timestamp = Date.now();
      } else {
        chats.push({
          id: chatId,
          title: generateTitle(messages),
          timestamp: Date.now(),
          messages: messages
        });
      }
      
      localStorage.setItem('rgs-chats', JSON.stringify(chats));
    }
  }, [messages, chatId]);

  const generateTitle = (msgs: ChatMessage[]) => {
    const firstUserMsg = msgs.find(m => m.role === 'user');
    if (firstUserMsg) {
      return firstUserMsg.content.split(' ').slice(0, 4).join(' ') + '...';
    }
    return 'New Chat';
  };

  const handleSendMessage = (content: string) => {
    const newMessage: ChatMessage = {
      id: Date.now(),
      role: 'user',
      content,
      timestamp: Date.now()
    };
    setMessages(prev => [...prev, newMessage]);
    
    // Simulate AI response (replace with your actual API call)
    setTimeout(() => {
      const aiResponse: ChatMessage = {
        id: Date.now() + 1,
        role: 'assistant',
        content: "I'm here to help! What would you like to know?",
        timestamp: Date.now()
      };
      setMessages(prev => [...prev, aiResponse]);
    }, 1000);
  };

  // Welcome screen when no messages
  if (messages.length === 0) {
    return (
      <div className="flex flex-col items-center justify-center h-full">
        <div className="text-center space-y-4">
          <div className="flex justify-center">
            <div className="p-4 rounded-full bg-gradient-to-r from-blue-600 to-cyan-500 text-white">
              <MessagesSquare className="h-12 w-12" />
            </div>
          </div>
          <h1 className="text-3xl font-bold bg-gradient-to-r from-blue-600 to-cyan-500 bg-clip-text text-transparent">
            RGS AI
          </h1>
          <p className="text-muted-foreground text-lg">
            How can I help you today?
          </p>
        </div>
        <div className="absolute bottom-24 left-0 right-0 px-4 max-w-3xl mx-auto">
          <MessageInput onSend={handleSendMessage} />
        </div>
      </div>
    );
  }

  return (
    <div className="flex flex-col h-full relative">
      {/* Messages Container */}
      <div className="flex-1 overflow-y-auto p-4 space-y-4">
        {messages.map((msg) => (
          <div
            key={msg.id}
            className={`flex ${msg.role === 'user' ? 'justify-end' : 'justify-start'}`}
          >
            <div
              className={`max-w-[80%] rounded-2xl px-4 py-2.5 ${
                msg.role === 'user'
                  ? 'bg-gradient-to-r from-blue-600 to-cyan-500 text-white'
                  : 'bg-muted text-foreground'
              }`}
            >
              <p className="whitespace-pre-wrap break-words">{msg.content}</p>
            </div>
          </div>
        ))}
      </div>

      {/* Input Area */}
      <div className="border-t bg-background/80 backdrop-blur-sm p-4">
        <div className="max-w-3xl mx-auto">
          <MessageInput onSend={handleSendMessage} />
        </div>
      </div>
    </div>
  );
};

export default Thread;